	export default {
		data() {
			return{
				orderId: '',
                productId:'',
                hospId:''
			}
		},
		onLoad(option) {
			this.orderId = option.id
            this.productId = option.productId
            this.hospId = option.hospId
		},
        onUnload() {
            let pages = getCurrentPages();
            console.log(pages.length)
            uni.navigateBack({
                delta:pages.length-3
            })
        },
		methods: {
			yuyue() {
				
                uni.navigateTo({
                    url:'/pages/service-record/makeMedical?backType=1&reserveStatus=null&hospId='+this.hospId + "&procudtId=" +this.productId + "&orderId=" +this.orderId
                })
			},
			chakan() {
				uni.navigateTo({
				    url:"/pages/order-detail/order-detail?from=1&orderid="+ this.orderId + '&type=&orderType=KK'
				})
			}
		}
	}
